	import {  
        mapMutations  
    } from 'vuex';
	
	export default{
		
		data(){
			return {
				loginName: '',
				password: '',
				loginPassword: '',
				resultData:{},
				logining: false
			}
		},
		onLoad(){
			console.log('login页面onLoad');
		},
		methods: {
			...mapMutations(['login']),
			inputChange(e){
				const key = e.currentTarget.dataset.key;
				this[key] = e.detail.value;
			},
			navBack(){
				uni.navigateBack({
					delta: 1
				});
			},
			toRegist(){
				this.$api.msg('去注册');
				uni.navigateTo({
					url: '/pages/public/register',
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			},
			toLoginByPhone(){
				uni.navigateTo({
					url: '/pages/public/loginByPhone',
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			},
			toForgetPwd(){
				uni.navigateTo({
					url: '/pages/public/forgetPwd',
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			},
			async toLogin(){
				this.logining = true;
				let loginPhone = this.loginPhone;
				let loginPassword = this.loginPassword;
				const {loginName, password} = this;
				/* 数据验证模块
				if(!this.$api.match({
					loginName,
					password
				})){
					this.logining = false;
					return;
				}
				*/
				const sendData = {
					loginName,
					password
				};
				const result = await this.$api.json('userInfo');
				if(result.status === 1){
					this.login(result.data);
                    uni.navigateBack();
				}else{
					this.$api.msg(result.msg);
					this.logining = false;
				}
			}
		},
	}
